<template>
  <div class="bg"><img :src="picUrl" class="bg-img" alt=""></div>
  <div class="music-detail-top">
    <div class="detail-top-left">
      <svg class="icon" aria-hidden="true" @click="changeDetailShow">
        <use xlink:href="#icon-zuojiantou"></use>
      </svg>
      <div class="left-marquee">
        <Vue3Marquee>{{ musicDetail.name || musicDetail.al.name }}</Vue3Marquee>
        <span v-for="item in musicDetail.ar || musicDetail.artists" :key="item.id">
          {{ item.name }}
        </span>
<!--        <svg class="icon" aria-hidden="true">-->
<!--          <use xlink:href="#icon-xiangyou1"></use>-->
<!--        </svg>-->
      </div>
    </div>
<!--    <div class="detail-top-right">-->
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-fenxiang"></use>-->
<!--      </svg>-->
<!--    </div>-->
  </div>
  <div class="music-detail-content" v-show="!showLyric" @click="showLyric = true">
    <img src="./images/needle-ab.png" alt="" class="img-needle" :class="{active: isPlaying}">
    <img src="./images/cd.png" alt="" class="img-cd">
    <img :src="picUrl" alt="" class="img-ar" :class="{active: isPlaying}">
  </div>
  <div class="music-lyric" ref="musicLyricDiv" v-show="showLyric" @click="showLyric = false">
    <p v-if="!musicLyricArr.length">暂无歌词</p>
    <p v-for="lyric in musicLyricArr"
       :key="lyric.id"
       :class="{active: currentPlayLyricIndex === lyric.id}">
      {{ lyric.lrc }}
    </p>
  </div>
  <div class="music-detail-bot">
<!--    <div class="bot-header">-->
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-w_aixin"></use>-->
<!--      </svg>-->
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-xiazai"></use>-->
<!--      </svg>-->
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-changpian"></use>-->
<!--      </svg>-->
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-icon-7"></use>-->
<!--      </svg>-->
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-xunhuanbofang"></use>-->
<!--      </svg>-->
<!--    </div>-->
    <div class="bot-content">
      <input type="range" :min="0" :max="duration" v-model="currentPlayTime" step="0.05">
    </div>
    <div class="bot-footer">
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-icon-5"></use>-->
<!--      </svg>-->
      <svg class="icon" aria-hidden="true" @click="changeMusic(-1)">
        <use xlink:href="#icon-icon-2"></use>
      </svg>
      <svg class="icon main" aria-hidden="true" v-if="isPlaying" @click="audioControl.stop">
        <use xlink:href="#icon-icon-3"></use>
      </svg>
      <svg class="icon main" aria-hidden="true" v-else @click="audioControl.play">
        <use xlink:href="#icon-icon-"></use>
      </svg>
      <svg class="icon" aria-hidden="true" @click="changeMusic(1)">
        <use xlink:href="#icon-icon-1"></use>
      </svg>
<!--      <svg class="icon" aria-hidden="true">-->
<!--        <use xlink:href="#icon-yinle-07"></use>-->
<!--      </svg>-->
    </div>
  </div>
</template>

<script>
import {Vue3Marquee} from 'vue3-marquee'
import 'vue3-marquee/dist/style.css'
import {computed, ref, watch} from 'vue'
import {useStore} from 'vuex'
import {Toast} from 'vant'

export default {
  name: 'MusicDetails',
  components: {
    Vue3Marquee,
  },
  props: {
    picUrl: String,
    musicDetail: Object,
    changeDetailShow: Function,
    audioControl: Object,
    isPlaying: Boolean,
  },
  emits: ['changeCurrentPlayTime'],
  setup(props, {emit}) {
    const store = useStore()
    //是否显示歌词
    const showLyric = ref(false)
    //当前音乐总时长
    const duration = computed(() => store.state.FooterMusic.duration)
    //当前播放时间(getter + setter)
    const currentPlayTime = computed({
      get() {
        return store.state.FooterMusic.currentPlayTime
      },
      set(value) {
        emit('changeCurrentPlayTime', value)
        return store.commit('FooterMusic/updateCurrentTime', value)
      }
    })
    //获取歌词对象
    const musicLyric = computed(() => store.state.FooterMusic.musicLyric)
    //处理歌词成数组
    const musicLyricArr = computed(() => {
      //将歌词以换行分成数组
      const resultArr = musicLyric.value.lyric?.split(/\n+/)
          //过滤掉没有值的数据（最后一行\n被拆空行，和歌词中无歌词内容）
          .filter(item => item && item.split(']')[1])
          //将每个字符串处理成对象
          .map((item, index) => {
            // console.log(item)
            //获取中括号中间时间，不要中括号(ios不支持正则断言)
            const time = /\[.*\]/.exec(item)[0].slice(1, -1)
            // console.log(time)
            //将中括号内容进行分割，分，秒，毫秒(给空字符，防止undefined上调用方法报错)
            const [m, s, ms] = time ? time.split(/[:\.]/) : ''
            // console.log(m, s, ms)
            //当前播放时间，单位毫秒，分钟计算毫秒，秒计算毫秒，最后加上毫秒（转换成number，否则会拼字符串）
            const playTime = parseInt((m * 60 * 1000) + (s * 1000) + (ms === undefined ? 0 : +ms))
            //获取歌词
            const lrc = item.split(']')[1] || ''
            return {
              id: index,
              lrc,
              playTime,
            }
          })
      //将每个歌词，添加下一个歌词的开始时间，方便后续计算
      resultArr?.forEach((item, index, arr) => {
        //当前不是末尾元素时，正常处理，末尾元素就返回当前播放时间加1s
        if (index < arr.length - 1) {
          item.nextTime = arr[index + 1].playTime
        } else {
          item.nextTime = item.playTime + 1000
        }
      })
      return resultArr
    })
    //返回当前播放歌词的id
    const currentPlayLyricIndex = computed(() => {
      //当前播放时间，转换毫秒
      const curTime = parseInt(currentPlayTime.value * 1000)
      //返回查找到的下标，歌词开始时间大于等于当前时间，并且小于下个歌词开始时间，返回下标
      return musicLyricArr.value.findIndex(item => (curTime >= item.playTime) && (curTime < item.nextTime))
    })

    //获取当前歌词外div
    const musicLyricDiv = ref(null)

    //当前下标歌词改变的时候，进行滚动，使歌词处于页面中间
    watch(currentPlayLyricIndex, () => {
      //如果没有元素就直接退出函数
      if(!musicLyricDiv.value.querySelector('.active')) {
        return
      }
      //当前歌词距离盒子顶部的距离
      const currentLyricTop = musicLyricDiv.value.querySelector('.active').offsetTop
      musicLyricDiv.value.scrollTo({
        top: parseInt(currentLyricTop - 200),
        behavior: 'smooth',
      })
    })

    //切换上下首
    function changeMusic(num) {
      //获取音乐列表个数
      const musicLen = store.state.FooterMusic.playList.length
      if (musicLen <= 1) {
        Toast('播放列表，暂无更多音乐')
        return
      }
      //获取当前下标
      let curIndex = store.state.FooterMusic.playListIndex
      //正常切换，第一首上一首返回播放列表最后一首，最后一首下一首返回播放列表第一首
      curIndex = curIndex + parseInt(num)
      curIndex = curIndex < 0 ? musicLen - 1 : curIndex % musicLen
      store.commit('FooterMusic/updatePlayListIndex', curIndex)
    }

    return {
      showLyric,// 是否显示歌词
      musicLyric, //歌词对象
      musicLyricArr, //处理后的歌词数组
      currentPlayLyricIndex, //当前播放歌词下标
      musicLyricDiv, //歌词div
      changeMusic, //切换上下首方法，接受数字参数，如上一首 -1 下一首 1
      currentPlayTime, //当前播放时间
      duration, //音频总时长
    }
  }
}
</script>

<style lang="less" scoped>
.bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #333;
  z-index: -1;

  .bg-img {
    position: absolute;
    max-width: 100%;
    max-height: 100%;
    opacity: .3;
    filter: blur(20px);
  }
}

.music-detail-top {
  height: 1rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .2rem;

  .icon {
    fill: white;
    width: .5rem;
    height: .5rem;
    vertical-align: middle;
  }

  .detail-top-left {
    display: flex;
    align-items: center;

    .left-marquee {
      width: 3rem;
      height: 100%;
      margin-left: .4rem;
      color: white;
      white-space: nowrap;

      span {
        color: #999;
        &:not(:last-of-type)::after {
          content: '、';
        }
      }

      .icon {
        fill: #999;
        width: .3rem;
        height: .3rem;
      }
    }
  }
}

.music-detail-content {
  width: 100%;
  height: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .img-needle {
    width: 2rem;
    position: absolute;
    left: 46%;
    transform-origin: 15px 15px;
    transform: rotate(-20deg);
    transition: all 2s;
    z-index: 3;
  }

  .img-needle.active {
    transform: rotate(0);
  }

  .img-cd {
    position: absolute;
    width: 5rem;
    height: 5rem;
    top: 20%;
    z-index: 2;
  }

  .img-ar {
    position: absolute;
    top: 25%;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    animation: 10s rotate_ar infinite linear;
    animation-play-state: paused;
  }

  .img-ar.active {
    animation-play-state: running;
  }

  @keyframes rotate_ar {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
}

.music-lyric {
  scroll-behavior: smooth;
  width: 100%;
  height: 50vh;
  display: flex;
  margin: 1rem auto;
  flex-direction: column;
  align-items: center;
  overflow: auto;

  p {
    transition: all .5s;
    color: #999;
    margin: .2rem .5rem;
    text-align: center;
  }

  p:first-of-type {
    margin-top: 200px;
  }

  p:last-of-type {
    margin-bottom: 200px;
  }

  p.active {
    text-shadow: 0 0 5px #333;
    color: white;
    font-size: 0.4rem;
  }
}

.music-detail-bot {
  position: fixed;
  z-index: 10;
  width: 100%;
  bottom: 0.6rem;
  .bot-header, .bot-footer {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;

    .icon {
      width: .5rem;
      height: .5rem;
      fill: white;
    }

    .icon.main {
      width: 1rem;
      height: 1rem;
    }
  }

  .bot-content {
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    input {
      width: 80%;
    }
  }
}
</style>
