import {reqLyric} from '@/api'

export default {
	namespaced: true,
	state: {
		//播放列表
		playList: JSON.parse(localStorage.getItem('playList')) || [],
		playListIndex: JSON.parse(localStorage.getItem('playListIndex')) || 0, // 当前播放下标，默认0
		isPlaying: false, // 是否播放中
		detailShow: false, // 播放详情是否显示
		musicLyric: {}, // 歌词
		currentPlayTime: 0, //当前播放时间
		duration: 0, //当前歌曲总时长
	},
	mutations: {
		//修改播放状态的函数
		changePlayStatus(state, value) {
			state.isPlaying = value
		},
		//修改播放详情是否显示
		changeDetailShow(state) {
			state.detailShow = !state.detailShow
		},
		//更新播放列表
		updatePlayList(state, value) {
			state.playList = value
			localStorage.setItem('playList', JSON.stringify(value))
		},
		//更新正在播放列表下标
		updatePlayListIndex(state, index) {
			state.playListIndex = index
			localStorage.setItem('playListIndex', JSON.stringify(index))
		},
		//保存歌词数据
		saveMusicLyric(state, data) {
			state.musicLyric = data
		},
		//更新当前播放时间
		updateCurrentTime(state, time) {
			state.currentPlayTime = time
		},
		//更新音乐总时长
		updateDuration(state, time) {
			state.duration = time
		},
	},
	actions: {
		async getMusicLyric({commit}, id) {
			try{
				const result = await reqLyric({id})
				commit('saveMusicLyric', result.lrc)
				return true
			}catch (e) {
				return Promise.reject(e.message)
			}
		}
	},
	getters: {},
}
