import {reqBanners, reqMusicMenu} from '@/api'

export default {
	namespaced: true,
	state: {
		banners: [],
		musicMenu: [],
	},
	mutations: {
		saveBanners(state, data) {
			state.banners = data
		},
		saveMusicMenu(state, data) {
			state.musicMenu = data
		}
	},
	actions: {
		async getBanners({commit}) {
			try{
				const result = await reqBanners()
				commit('saveBanners', result.banners)
				return true
			}catch (e) {
				return Promise.reject(e.message)
			}
		},
		async getMusicMenu({commit}) {
			try{
				const result = await reqMusicMenu()
				commit('saveMusicMenu', result.result)
				return true
			}catch (e) {
				return Promise.reject(e.message)
			}
		}
	},
	getters: {
	},
}
