<template>
    <div class="footer-music">
      <div class="footer-music-left" @click="changeDetailShow">
        <van-image :src="picUrl" class="music-pic" round>
          <template v-slot:error>加载失败</template>
        </van-image>
        <div>
          <Vue3Marquee class="music-name">{{ currentPlay?.name || currentPlay?.al?.name || '暂无播放音乐列表' }}</Vue3Marquee>
        </div>
      </div>
      <div class="footer-music-right">
        <svg class="icon" aria-hidden="true" v-if="!isPlaying" @click="audioControl.play">
          <use xlink:href="#icon-yunhang"></use>
        </svg>
        <svg class="icon" aria-hidden="true" v-else @click="audioControl.stop">
          <use xlink:href="#icon-zanting"></use>
        </svg>
<!--        <svg class="icon" aria-hidden="true">-->
<!--          <use xlink:href="#icon-duoxuankuang"></use>-->
<!--        </svg>-->
      </div>
      <audio ref="audio" v-if="currentPlay?.id"></audio>
<!--      <audio ref="audio" v-if="currentPlay?.id" :src="`https://music.163.com/song/media/outer/url?id=${currentPlay.id}.mp3`"></audio>-->
    </div>
    <van-popup v-model:show="detailShow" position="bottom" :style="{ height: '100%', width: '100%' }" v-if="currentPlay">
      <MusicDetail :picUrl="picUrl"
                   :musicDetail="currentPlay"
                   :changeDetailShow="changeDetailShow"
                   :audioControl="audioControl"
                   :isPlaying="isPlaying"
                   @changeCurrentPlayTime="changeCurrentPlayTime"/>
    </van-popup>
</template>

<script>
import {computed, nextTick, onMounted, onUpdated, reactive, ref, watch, watchEffect} from 'vue'
import {useStore} from 'vuex'
import MusicDetail from '@/components/VFooterMusic/MusicDetail'
import {Vue3Marquee} from 'vue3-marquee'
import 'vue3-marquee/dist/style.css'
import {Toast} from 'vant'
import {reqMusicUrl} from '@/api'

export default {
  name: 'FooterMusic',
  components: {
    MusicDetail,
    Vue3Marquee,
  },
  setup() {
    //获取audio元素
    const audio = ref(null)
    //使用vuex配合计算属性获取数据
    const store = useStore()
    //获取播放列表
    const playList = computed(() => store.state.FooterMusic.playList)
    //获取当前播放下标
    const playListIndex = computed(() => store.state.FooterMusic.playListIndex)
    //将上两个数据结合，减少每次模板中写playList[playListIndex]
    const currentPlay = computed(() => {
      //ref对象要用value，经常忘记写
      return playList.value[playListIndex.value]
    })
    //返回图片
    const picUrl = computed(() => {
      return currentPlay.value?.al?.picUrl || currentPlay.value?.album?.artist?.img1v1Url
    })
    //获取播放状态
    const isPlaying = computed(() => store.state.FooterMusic.isPlaying)
    //获取播放详情是否展示
    const detailShow = computed(() => store.state.FooterMusic.detailShow)
    //当挂载，或者数据更新时通知vuex，获取歌词
    function updateMusicLyc () {
      if (currentPlay.value?.id) {
        store.dispatch('FooterMusic/getMusicLyric', currentPlay.value.id)
        audioBindEvent()
      }
    }
    onMounted(() => {
      updateMusicLyc()
    })
    onUpdated(() => {
      updateMusicLyc()
    })

    //获取修改播放详情是否显示的方法
    function changeDetailShow() {
      //如果当前有音乐的时候切换
      if(currentPlay) {
        store.commit('FooterMusic/changeDetailShow', changeDetailShow)
      }
    }

    //audio控件的方法
    const audioControl = reactive({
      play() {
        try{
          if (audio.value) {
            nextTick(() => {
              audio.value.autoplay = true
              audio.value.play()
              store.commit('FooterMusic/changePlayStatus', true)
            })
          } else {
            Toast('暂无正在播放音乐')
          }
        }catch (err) {
          Toast({
            type: 'fail',
            message: '歌词有异常，无法播放'
          })
          store.commit('FooterMusic/changePlayStatus', false)
        }
      },
      stop() {
        if (audio.value) {
          audio.value.pause()
          store.commit('FooterMusic/changePlayStatus', false)
        }
      }
    })
    //监听下标或播放列表改变时，获取音乐URL自动播放
    watch([playListIndex, playList, currentPlay], () => {
      nextTick(async () => {
        const result = await reqMusicUrl({id: currentPlay.value.id})
        result.data?.length > 0 ? audio.value.src = result.data[0].url : ''
        audioControl.play()
      })
    }, {deep: true})

    //如果修改了时间，就触发该事件，来修改音频播放时间
    function changeCurrentPlayTime(value) {
      audio.value.currentTime = value
    }
    //事件绑定，用匿名函数可能会重复绑定，所以采用普通函数
    //修改音频总时长
    function updateDuration() {
      store.commit('FooterMusic/updateDuration', audio.value.duration)
    }
    //修改当前播放时间
    function updateCurrentTime() {
      store.commit('FooterMusic/updateCurrentTime', audio.value.currentTime)
    }
    //播放完毕操作
    function playEnd() {
      store.commit('FooterMusic/changePlayStatus', false)
      //切换下一首，如果这一首是最后一首，返回第一首
      store.commit('FooterMusic/updatePlayListIndex', (playListIndex.value + 1) % playList.value.length)
      audioControl.play()
    }
    //音频事件绑定
    function audioBindEvent() {
      //更新音频总时长
      audio.value.addEventListener('durationchange', updateDuration)
      //当音频播放时间改变时,更新当前时间
      audio.value.addEventListener('timeupdate', updateCurrentTime)
      //当播放完毕时候，改为暂停，切换下一首
      audio.value.addEventListener('ended', playEnd)
    }
    return {
      picUrl, //图片
      currentPlay, //当前播放对象，将上两个结合 playList[playListIndex]
      audio, //音频控件
      audioControl, //音频控制对象（调用里面的方法控制音频）
      isPlaying, //当前播放状态
      detailShow, //列表展示
      changeDetailShow, //修改列表展示状态的方法
      changeCurrentPlayTime, //控制当前播放时间的方法，需要传入修改的时间
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ body {
  margin-bottom: 1.4rem;
}

.footer-music {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 1.4rem;
  background-color: white;
  position: fixed;
  bottom: 0;
  padding: .2rem;
  border-top: #999 solid 1px;

  .footer-music-left {
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .music-pic {
      width: 1rem;
      height: 1rem;
    }

    .music-name {
      width: 3rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: break-word;

    }
  }

  .footer-music-right {
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .icon {
      width: 0.6rem;
      height: 0.6rem;
    }
  }
}
</style>
