import request from '@/api/request'
// Home组件
// 获取banner
export const reqBanners = () => request({
	url: '/banner?type=2',
	method: 'get',
})
// 获取歌单列表
export const reqMusicMenu = () => request({
	url: '/personalized',
	method: 'get',
	params: {
		limit: 10,
	}
})
//获取歌单评论(因为接口需要验证，无法实现)
export const reqPlaylistComment = params => request({
	url: '/comment/playlist',
	method: 'get',
	params: {
		...params,
		limit: 20,
	},
})

// ItemMusic组件
// 获取详情信息
export const reqPlayListDetail = params => request({
	url: '/playlist/detail',
	method: 'get',
	params
})
//获取歌单详情的音乐列表
export const reqDetailMusicList = params => request({
	url: '/playlist/track/all',
	method: 'get',
	params: {
		limit: 20,
		...params,
	}
})

//全局组件FooterMusic
//获取音乐地址
export const reqMusicUrl = params => request({
	url: '/song/url',
	method: 'get',
	params
})
//获取歌词
export const reqLyric = params => request({
	url: '/lyric',
	method: 'get',
	params,
})

//搜索组件Searchc
//搜索
export const reqSearch = params => request({
	url: '/search',
	method: 'get',
	params,
})

