import {reqDetailMusicList, reqPlayListDetail} from '@/api'

export default {
	namespaced: true,
	state: {
		detail: {},
		detailMusicList: [],
	},
	mutations: {
		saveDetail(state, data) {
			state.detail = data
		},
		saveDetailMusicList(state, data) {
			state.detailMusicList = data
		}
	},
	actions: {
		async getDetail({commit}, id) {
			try {
				const result = await reqPlayListDetail({id})
				commit('saveDetail', result.playlist)
				return true
			} catch (e) {
				return Promise.reject(e.message)
			}
		},
		async getDetailMusicList({commit}, id) {
			try {
				const result = await reqDetailMusicList({id})
				commit('saveDetailMusicList', result.songs)
				return true
			} catch (e) {
				return Promise.reject(e.message)
			}
		}
	},
	getters: {
		creator(state) {
			return state.detail.creator
		}
	},
}
